  .lds-facebook {
    display: inline-flex;
    position: relative;
    width: 0px;
    height: 0px;
  }
  .lds-facebook div {
    display: inline-block;
    position: absolute;
    width: 2px;
    background: rgb(114, 114, 114);
    -webkit-animation: lds-facebook 1.0s cubic-bezier(0, 0.3, 0.3, 0.6) infinite;
            animation: lds-facebook 1.0s cubic-bezier(0, 0.3, 0.3, 0.6) infinite;
  }
  .lds-facebook div:nth-child(1) {
    left: -10px;
    -webkit-animation-delay: -0.24s;
            animation-delay: -0.24s;
  }
  .lds-facebook div:nth-child(2) {
    left: -4px;
    -webkit-animation-delay: -0.12s;
            animation-delay: -0.12s;
  }
  .lds-facebook div:nth-child(3) {
    left: 2px;
    -webkit-animation-delay: 0;
            animation-delay: 0;
  }
  @-webkit-keyframes lds-facebook {
    0% {
      top: -4px;
      height: 12px;
    }
    50%, 100% {
      top: -2px;
      height: 6px;
    }
  }
  @keyframes lds-facebook {
    0% {
      top: -4px;
      height: 12px;
    }
    50%, 100% {
      top: -2px;
      height: 6px;
    }
  }

